html {
	height: 100%;
}

body {
	background-color: #efefef;
	margin: 0;
	min-width: 100%;
	height: 100%;
	font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
	font-size: 1em;

}

.fullscreen {
	width: 100%;
	height: 100%;
}

.sidebar {
	position: fixed;
	box-sizing: border-box;
	z-index: 1;
	background-color: #444;
	color: #eee;
	margin: 0;
	top: 0;
	left: 0;
	overflow-x: hidden;
	width: 18em;
	height: 100%;
}

a {
	color: blue;
	text-decoration: none;
}

.sidebar h1 {
	padding: 25px;
}

.sidebar-link {
	padding-left: 10px;
	padding-right: 10px;
	line-height: 1.7;
	display: block;
	width: 100%;
	padding-top: 10px;
	padding-bottom: 10px;
	color: #eee;
}

.sidebar-link:hover {
	background-color: black;
	opacity: 0.1;
}

.sidebar-link.active {
	background-color: black;
	opacity: 0.3;
}

.content {
	box-sizing: border-box;
	color: #222;
	margin: 0;
	padding: 25px;
	text-align: left;
	position: absolute;
	top: 0;
	left: 18em;
}

.content h1 {
	font-size: 2em;
}

.content ul {
	line-height: 1.4em;
}

li .stars {
	float: right;
}

.profile p {
	font-size: 1.1em;
	margin-bottom: 10px;
}

.projects {
	display: block;
}

.projects .section {
	min-height: 20em;
	margin-top: 2em;
	margin-bottom: 2em;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-flex-wrap: nowrap;
	        flex-wrap: nowrap;
	-webkit-justify-content: flex-start;
	        justify-content: flex-start;
	-webkit-align-items: flex-start;
	        align-items: flex-start;
}

.video-wrapper {
	-webkit-flex-grow: 0;
	        flex-grow: 0;
	-webkit-flex-shrink: 0;
	        flex-shrink: 0;
	-webkit-flex-basis: 30%;
	        flex-basis: 30%;
	min-width: 200px;
	max-width: 400px;
}

.video-description-wrapper {
	-webkit-flex-grow: 1;
	        flex-grow: 1;
	-webkit-flex-shrink: 1;
	        flex-shrink: 1;
	-webkit-flex-basis: 70%;
	        flex-basis: 70%;
	max-width: 75em;
	vertical-align: top;
}

.video-description {
	margin: 1em;
	font-size: 1.15em;
	text-align: left;
	vertical-align: top;
	max-width: 100%;
}

.table-padding {
	display: table-cell;
	min-width: 99%;
}

video {
	object-fit: cover;
	height: auto;
	max-width: 100%;
}

